<template>
    <div id="app">
        <vs-row
            vs-align="flex-start"
            vs-type="flex"
            vs-justify="center"
            vs-w="12"
        >
            <vs-col
                vs-type="flex"
                vs-justify="center"
                vs-align="center"
                vs-w="3"
            >
                <vs-card>
                    <div slot="header">
                        <h3>–ö–∞—Ç–µ–≥–æ—Ä–∏–∏</h3>
                    </div>
                    <div>
                        <vs-alert :active="!categories.length">
                            –°–ø–∏—Å–æ–∫ –∫–∞—Ç–µ–≥–æ—Ä–∏–π –µ—â—ë –Ω–µ –ø–æ–ª—É—á–µ–Ω
                        </vs-alert>
                        <categories
                            :categories="categories"
                            category_id_parent="0"
                            :apiCategorySub="apiCategory"
                        ></categories>
                    </div>
                </vs-card>
            </vs-col>
            <vs-col
                vs-justify="center"
                vs-align="center"
                vs-offset="1"
                vs-w="7"
            >
                <vs-card>
                    <div slot="header">
                        <h3>–°–ø–∏—Å–æ–∫ –Ω–æ–≤–æ—Å—Ç–µ–π</h3>
                    </div>
                    <div>
                        <vs-alert
                            :active="!categor—ÉNews.length && !activeCategoryId"
                        >
                            –í—ã–±–µ—Ä–∏—Ç–µ –∫–∞—Ç–µ–≥–æ—Ä–∏—é –¥–ª—è –≤—ã–≤–æ–¥–∞ –Ω–æ–≤–æ—Å—Ç–µ–π
                        </vs-alert>
                        <vs-alert
                            :active="!categor—ÉNews.length && activeCategoryId"
                        >
                            –í –∫–∞—Ç–µ–≥–æ—Ä–∏–∏ –Ω–µ—Ç –Ω–æ–≤–æ—Å—Ç–µ–π
                        </vs-alert>
                        <vs-button
                            v-for="news in categor—ÉNews"
                            :key="news.id"
                            @click="apiNews(news.id)"
                            class="news"
                            >{{ news.title }}</vs-button
                        >
                    </div>
                </vs-card>
                <div v-show="news">
                    <vs-divider />
                    <vs-card>
                        <div slot="header">
                            <h3>{{ news.title }}</h3>
                        </div>
                        <div>
                            <p class="mb-5">{{ news.content }}</p>
                            <vs-row vs-justify="flex-end">
                                <vs-chip color="#f96854"
                                    >–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –ø—Ä–æ—Å–º–æ—Ç—Ä–æ–≤:
                                    <strong>{{
                                        news.count_reads
                                    }}</strong></vs-chip
                                >
                                <vs-chip color="#24c1a0"
                                    >–°–µ–π—á–∞—Å —Å–º–æ—Ç—Ä—è—Ç –Ω–æ–≤–æ—Å—Ç—å:
                                    <strong>{{
                                        news.online_reads < 1
                                            ? "?"
                                            : news.online_reads
                                    }}</strong></vs-chip
                                >
                            </vs-row>
                        </div>
                    </vs-card>
                </div>
            </vs-col>
        </vs-row>
    </div>
</template>

<script>
document.title = "TZ-News";
import axios from "axios";
import io from "socket.io-client";
import categories from "./components/Categories";
var socket = io("https://tz-news.ip-marchenko.ru:3001", {
    transports: ["websocket", "polling", "flashsocket", "ws", "flash"],
});
export default {
    name: "App",
    data() {
        return {
            activeCategoryId: false,
            categories: [],
            categor—ÉNews: [],
            news: false,
        };
    },
    created() {
        socket.on("COUNT_READS", (data) => {
            if (this.news && this.news.id == data.id)
                this.news.count_reads = data.count_reads;
        });
        socket.on("ONLINE_READS", (data) => {
            for (let newsid in data) {
                if (this.news && this.news.id == newsid)
                    this.news.online_reads = data[newsid];
            }
        });
    },
    mounted() {
        var inside = this;
        axios.get("/api/categories").then(function (response) {
            inside.categories = response.data;
        });
    },
    methods: {
        apiCategory(category_id) {
            var inside = this;
            axios.get("/api/category/" + category_id).then(function (response) {
                inside.categor—ÉNews = response.data;
                inside.news = false;
                inside.activeCategoryId = category_id;
            });
        },
        apiNews(news_id) {
            var inside = this;
            axios.get("/api/news/" + news_id).then(function (response) {
                response.data.online_reads =
                    inside.news && inside.news.id == news_id
                        ? inside.news.online_reads
                        : 0;
                inside.news = response.data;
            });
        },
    },
    watch: {
        "news.id"(val) {
            socket.emit("read news", val);
        },
    },
    components: {
        categories,
    },
};
</script>
<style>
.category,
.news {
    display: inline-table;
    margin-top: 10px;
    width: 100%;
}
</style>
